@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.quiz-parent {
  width: 100vw;
  height: 100vh;
  background-color: rgb(39, 51, 87);
  position: relative;
}

.quiz-cont {
  width: 70vw;
  height: 60vh;
  background-color: azure;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: grid;
  place-items: center;
}

.head {
  padding-top: 30px;
  display: grid;
  place-items: center;
  font-size: 35px;
  font-family: serif;
  font-weight: 100;
}

#numbers {
  position: relative;
  right: 90px;
  float: left;
  color: black;
  font-size: 20px;
  font-weight: 300;
  letter-spacing: normal;
  font-family: serif;
}

#question {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 20px;
  font-size: 20px;
  font-family: 'Roboto', sans-serif;
}

.options-container {
  display: grid;
  place-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 650px;
  height: 200px;
}

.option-cont-1 {
  margin-bottom: -70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}
.option-cont-2 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}

.options-container button {
  width: 350px;
  height: 40px;
  margin: 10px;
  outline: none;
  border: none;
  border-radius: 30px;
  background-color: rgb(30, 144, 255);
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  cursor: pointer;
}

.controls {
  padding-bottom: 50px;
}

.controls #prev {
  margin-right: 25px;
  width: 90px;
  height: 30px;
  background-color: rgb(30, 144, 255);
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  border: none;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

.controls #next {
  margin-right: 25px;
  width: 70px;
  height: 30px;
  background-color: green;
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  border: none;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

.controls #quit {
  margin-right: 25px;
  width: 70px;
  height: 30px;
  background-color: red;
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  border: none;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

